@import '~antd/dist/antd.css';
*{
	margin: 0;
	padding: 0;
}
html{
	height: 100%;
}
body {
	font-size: 12px;
	height: 100%;
	color: #333;
	background-color: #f8f8f8;	
	font-family: "Helvetica Neue,Tahoma,Arial,PingFangSC-Regular,Hiragino Sans GB,Microsoft Yahei,sans-serif";
}
a {
	color: #333;
}
a,a:active,a:focus,a:hover,a:visited {
	text-decoration: none;
}
ul,li {
	margin: 0;
	padding: 0;
	list-style: none;
}
textarea {
	border: 0;
	outline: none;
}
button {
	outline: none;
}
input {
	outline: none;
	border: none;
}
img {
	max-width: 100%;
}
p{
	margin: 0;
	padding: 0;
}
.clearfix::after {
    *zoom: 1;
	clear: both;
}
.display-flex {
    display: -webkit-box;
    display: flex;
}
.flex-nowrap{
	flex-wrap: nowrap;
}
.flex-wrap{
	flex-wrap: wrap;
}
.align-center {
    -webkit-box-align: center;
    align-items: center;
}
.justify-space-between {
    justify-content: space-between;
}
.justify-start {
	-webkit-box-pack: start;
    justify-content: flex-start;
}
.justify-center {
    justify-content: center;
}
.justify-end {
	-webkit-box-pack: end;
    justify-content: flex-end;
}
.flex-1{
	-webkit-box-flex: 1;
	flex: 1;
}
.img-cover {
	width: 100%;
	height: 100%;
}
.hidden {
	overflow: hidden;
}
.fl {
	float: left;
}
.fr {
	float: right;
}

.bor-0 {
  border: none !important;
}

.bor-1 {
  border: 1px solid #ccc;
}

.bor-b-1 {
  border-bottom: 1px solid #ccc !important;
}

.bor-r-1 {
  border-right: 1px solid #ccc !important;
}

.bor-l-1 {
  border-left: 1px solid #ccc !important;
}

.bor-t-1 {
  border-top: 1px solid #ccc !important;
}
.inline-block {
	display: inline-block;
}
.text-center {
	text-align: center;
}
.text-left {
	text-align: left;
}
.text-right {
	text-align: right;
}

.line-2-overflow,
.line-3-overflow {
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-box;
  display: -o-box;
  -webkit-box-orient: vertical;
}

.line-1-overflow {
  white-space: nowrap;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  overflow: hidden;
}

.line-2-overflow {
  -webkit-line-clamp: 2;
}

.line-3-overflow {
  -webkit-line-clamp: 3;
}

.text-ellipsis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  white-space: nowrap;
}
